html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

html {
  position: relative;
  box-sizing: border-box;
  min-height: 100%;
}

::-ms-clear {
  display: none;
}

*:focus, *:visited, *:active, *:hover {
  outline: 0 !important;
}

*:-moz-focus-inner {
  border: 0;
}

body {
  background: #FFF;
  line-height: 2;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 15px;
}

.out {
  position: relative;
}

.wrapper {
  position: relative;
  overflow: hidden;
  padding: 0;
  min-width: 320px;
  opacity: 1;
}

.container {
  position: relative;
  max-width: 1130px;
  margin: 0 auto;
}

@media only screen and (max-width: 1130px) {
  .container {
    padding: 0 10px;
  }
}

a {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  text-decoration: none;
}

a:visited, a:hover, a:active {
  outline: none !important;
  text-decoration: none !important;
}

.test {
  size: 500px;
}

@media only screen and (max-width: 599px) {
  .test {
    size: 303px;
  }
}

.hero {
  background: url(../img/bg-hero2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  background-position: 50% 100%;
  color: #fff;
  min-height: 800px;
  height: 800px;
  position: relative;
}

.hero .container {
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 26px 0;
}

.header .right {
  display: flex;
  align-items: center;
  margin-left: auto;
}

nav {
  margin-right: 25px;
}

nav ul {
  display: flex;
}

nav li {
  margin: 0 25px;
  font-size: 14px;
  transition: all .3s ease;
}

nav li:hover {
  color: #26fcea;
}

.logo {
  font-size: 20px;
  font-weight: 400;
}

.logo span {
  color: #26fcea;
}

.callme {
  font-size: 14px;
  width: 175px;
  height: 46px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.07);
  border-radius: 30px;
  line-height: 46px;
  text-align: center;
  padding: 0 30px 0 65px;
  position: relative;
}

.callme:before {
  content: '';
  position: absolute;
  width: 46px;
  height: 46px;
  box-shadow: 1px 0 0 rgba(255, 255, 255, 0.1), inset -1px 0 0 rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  left: 0;
  top: 0;
  background: url(../img/phone.svg);
  background-repeat: no-repeat;
  background-size: 15px 15px;
  background-position: 50% 50%;
}

.title__main {
  font-size: 40px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
  margin-bottom: 0.9em;
}

.title__benefits {
  font-size: 22px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
}

.blue {
  color: #26fcea;
}

.between {
  margin: 0 10px;
}

.between span {
  size: 5px;
  border-radius: 50%;
  background: #26fcea;
  margin: 0 2px;
  display: inline-block;
  vertical-align: middle;
}
