
$color: #3b4b73;
$accent: #26fcea;
$color1: #fff;


.hero {
	background: url(../img/bg-hero2.jpg);
	@include cover;
	background-position: 50% 100%;
	color: $color1;
	min-height: 800px;
	height: 800px;
	position: relative;

	.container {
		min-height: 100%;
		display: flex;
		justify-content: center;
		align-items: center; } }


.header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	display: flex;
	align-items: center;
	padding: 26px 0;
	.right {
		display: flex;
		align-items: center;
		margin-left: auto; } }

nav {
	margin-right: 25px;
	ul {
		display: flex; }
	li {
		margin: 0 25px;
		font-size: 14px;
		transition: all .3s ease;
		&:hover {
			color: $accent; } } }
.logo {
	font-size: 20px;
	font-weight: 400;
	span {
		color: $accent; } }
.callme {
	font-size: 14px;
	width: 175px;
	height: 46px;
	box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.08);
	border: 1px solid rgba(255, 255, 255, 0.07);
	border-radius: 30px;
	line-height: 46px;
	text-align: center;
	padding: 0 30px 0 65px;
	position: relative;
	&:before {
		content: '';
		position: absolute;
		width: 46px;
		height: 46px;
		box-shadow: 1px 0 0 rgba(255, 255, 255, 0.1), inset -1px 0 0 rgba(0, 0, 0, 0.15);
		border-radius: 50%;
		left: 0;
		top: 0;
		background: url(../img/phone.svg);
		background-repeat: no-repeat;
		background-size: 15px 15px;
		background-position: 50% 50%; } }
.title__main {
	font-size: 40px;
	font-weight: 300;
	line-height: 50px;
	text-align: center;
	margin-bottom: 0.9em; }
.title__benefits {
	font-size: 22px;
	font-weight: 300;
	line-height: 50px;
	text-align: center; }
.blue {
	color: $accent; }

.between {
	margin: 0 10px;
	span {
		size: 5px;
		border-radius: 50%;
		background: $accent;
		margin: 0 2px;
		display: inline-block;
		vertical-align: middle; } }
